html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

body {
  font-family: -apple-system,'BlinkMacSystemFont','Segoe UI','Roboto','Lato','Helvetica','Arial',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

.plot-container {
  height: 100%;
}

.bg {
  fill: #FFF;
}

.axis line {
  stroke: #000;
}

.axis text {
  fill: #000;
}

@media (prefers-color-scheme: dark) {
  .bg {
    fill: #202122;
  }
  .axis line {
    stroke: #FFF;
  }
  .axis text {
    fill: #FFF;
  }
}